<template>
	<view class="my-search-container" :style="{ 'background-color': bgcolor }" @click="searchBoxHandler">
		<view class="my-search-box" :style="{ 'border-radius': radius + 'px' }">
			<uni-icons type="search" size="17"></uni-icons>
			<text class="placeholder">搜索</text>
		</view>
	</view>
</template>

<script>
export default {
	name: 'my-search',
	props: {
		bgcolor: {
			type: String,
			default: '#5049d3'
		},
		radius: {
			type: Number,
			default: 18
		}
	},
	data() {
		return {};
	},
	methods: {
		searchBoxHandler() {
			this.$emit('click');
		}
	}
};
</script>

<style lang="scss">
.my-search-container {
	display: flex;
	align-items: center;
	height: 50px;
	padding: 0 10px;

	.my-search-box {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 36px;
		width: 100%;
		background-color: #fff;

		.placeholder {
			margin-left: 5px;
			font-size: 15px;
		}
	}
}
</style>
